// }



let words_info = {}
let blends_so_far = {}
let specials_so_far = {}
for (const [group, words] of Object.entries(word_groups)) {
  for (const word of words){
    let blends = []
    for (let blend of blend_kinds){
      if(word.includes(blend)){
        blends.push(blend)
        blends_so_far[blend] = true
      }
    }
    let specials = []
    for(let sp of special_kinds){
      let r = new RegExp(sp)
      if(r.test(word)){
        specials.push(sp)
        specials_so_far[sp] = true
      }
    }
    if(group.includes("//")){
      specials.push(group.split("//")[1])
    }

    let digraphs = []
    for(let dg of digraph_kinds){
     if(word.includes(dg)){
      digraphs.push(dg.toUpperCase())  
     } 
    }

    let info = {
      graphemes : digraphs.concat(word.split("").map((c)=>c.toUpperCase())),
      blends : blends.map((bl)=>bl.toUpperCase()),
      specials : specials.map((bl)=>bl.toUpperCase()),
      group : group,
      allowed_graphemes : group_allowed_graphemes[group],
    }
    words_info[word.toLowerCase()] = info
  }
  group_allowed_graphemes[group]['blends'] = [...Object.keys(blends_so_far)]
  group_allowed_graphemes[group]['specials'] = [...Object.keys(specials_so_far)]
}
export {words_info};

console.log("words_info", words_info)



const NUM_LETTERS = 8
/* Randomize array in-place using Durstenfeld shuffle algorithm */
function shuffleArray(array) {
    for (var i = array.length - 1; i > 0; i--) {
        var j = Math.floor(Math.random() * (i + 1));
        var temp = array[i];
        array[i] = array[j];
        array[j] = temp;
    }
    return array
}

let vowels = shuffleArray(["A", "E", "I", "O", "U"])
let ee_vowels = [...vowels]
ee_vowels.splice(vowels.indexOf("O"),1,"E")

let consonants = shuffleArray(["B", "D", "L", "N", "P", "R", "S", "T",  "Y"])
let ll_consonants = [...consonants]
ll_consonants.splice(consonants.indexOf("T"),1,"L")

let options =  [...consonants, ...vowels]
let ee_options =  [... consonants, ...ee_vowels]   //+ shuffleArray(["B", "D", "L", "N", "P", "R", "S", "T",  "Y"])
let ll_options =  [...ll_consonants, ...vowels]  //shuffleArray(["A", "E", "I", "O", "U"]) + shuffleArray(["B", "D", "L", "N", "P", "R", "S", "T",  "Y"])

let grapheme_options = {}
// Object.entries(grapheme_options).forEach(([word,graphemes]) =>{
all_words.forEach((word) =>{
  let info = words_info[word.toLowerCase()]
  let allowed_graphemes = info.allowed_graphemes
  let graphemes = [...allowed_graphemes.consonants,...allowed_graphemes.vowels]
  let word_letters = graphemes.filter((x)=>info.graphemes.indexOf(x) !=- 1)
  let wl_copy = [...word_letters]
  let w = word.toLowerCase()
  for(let wl of wl_copy){
    w = w.replace(wl.toLowerCase(),"")
    // console.log(w,wl.toLowerCase())
  }
  let extra_letters = []
  for(let l of w){
    extra_letters.push(l.toUpperCase())
  }

  
  let non_word_letters = graphemes.filter((x)=>!word.includes(x))
  let subset = shuffleArray(non_word_letters).slice(0,Math.max(NUM_LETTERS-word.length,0))
  subset = subset.concat(word_letters)
  grapheme_options[word]= graphemes.filter((x)=>subset.includes(x)).concat(extra_letters)
  // console.log(word, grapheme_options[word])
})
console.log("grapheme_options", grapheme_options)

export {grapheme_options};
