import React, {Component} from 'react';
import { StyleSheet, Text, View, Platform } from 'react-native';
import Svg, { Line, Path, Circle } from 'react-native-svg';


let percent_to_svg = (p,direction=0,radius=30) => {
  p = p % 1
  let r = ( 2 * Math.PI * p)
  // console.log(r)
  let mul = direction ? 1 : -1
  let x = radius + (Math.sin( r ) * radius * mul)
  let y = radius + (Math.cos( r ) * - radius)
  let mid = ( p < .5 ) ? 0 : 1
  return `M ${radius} ${radius} v -${radius} A ${radius} ${radius} 1 ${mid} ${direction} ${x} ${y} z` 
}

class Timer extends Component {
  constructor(props){
    // console.log("CONSTR", props)
    super(props);
    this.state = {
      elapsed_n_sec : 0,
      perc_remaining : 1.0
    }
    this.tick_every = this.props.tick_every || 1
    this.radius = this.props.radius || 30
  }

  scheduleNextTick(){
    setTimeout(()=> {
      let e = this.state.elapsed_n_sec + this.tick_every
      let p = (this.props.n_sec - e) / this.props.n_sec
      // console.log("TICK", e, p)
      if(p < 0){p = 0}
      this.setState({"elapsed_n_sec" : e, "perc_remaining" : p})
      if(p <= 0){
        if(this.props.onDone){
          this.props.onDone()
        }
      }else{
        this.scheduleNextTick()
      }
    },this.tick_every*1000)
  }

  componentDidMount() {
    this.scheduleNextTick()
  }


  render(){
      // console.log(props)
      let path = percent_to_svg(this.state.perc_remaining, 0, this.radius)
      let is_full = this.state.perc_remaining == 1
      let remaining = this.props.n_sec - this.state.elapsed_n_sec
      let text = Math.ceil(remaining / 60)
      // console.log("RENDER!!!", remaining)

      let size_style = {width: this.radius*2, height: this.radius*2}

      return (
        <View style={[size_style, this.props.style]}>
          <Svg>
            {is_full && 
              <Circle cx={this.radius} cy={this.radius} r={this.radius} fill='#ccf'/> 
              || <Path d ={path} fill='#ccf'/>
            }
            <Circle cx={this.radius} cy={this.radius} r={this.radius*.6} fill="#fff"/>
          </Svg>
          <View style={[size_style, styles.timer_text_container]}>
            <Text style={[{fontSize:this.radius*.7},styles.timer_text]}>{text}</Text> 
          </View>
        </View>
      )
  }
}

const styles = StyleSheet.create({
  timer_text_container : {
    position : "absolute",
    top : 0,
    top : 0,
    flex : 1,
    // backgroundColor: '#f00',
    // width:  "100%",
    // height:  "100%",
    
    alignItems : "center",
    justifyContent : "center"
  },
  timer_text : {
    color : '#99a'
  }
})

export default Timer;
