//Applies modified version of Levenshtein distance to find best sequence
// of corrections
export function best_correction_sequence(word, spelling, prioritize_consonants=false, return_break_down=false){
	// console.log(word,spelling)
	if(Array.isArray(word)){word = word.join("")}
	if(Array.isArray(spelling)){spelling = spelling.join("")}
    var a = spelling, m = [], e=[], i, j, min = Math.min;
	
	// console.log(word.toUpperCase())
    let phonetic_spelling = word_phonemes[word.toUpperCase()]
    if(!phonetic_spelling){
    	alert("Missing Phonemes for word: "+ word)
    }
    console.log(phonetic_spelling)
    
    let b = [...phonetic_spelling]

    let w_bd = (new PartialSpelling(word.toUpperCase(),phonetic_spelling,[],[])).find_best_break_down()
    console.log(w_bd)
    if(!w_bd){
    	alert("Missing Breakdown: "+ word)	
    }
    // let w_ps = best_partial_spelling(word,word)


    // console.log("w_bd", w_bd)

    for (i = 0; i <= a.length; i++){
    	m.push([])
    	e.push([])
    }

    //Init matrices
    let start_ps = new PartialSpelling(spelling.toUpperCase(),phonetic_spelling,[],[])
    let ps = start_ps
    m[0][0] = ps
    e[0][0] = 0
    for (i = 1; i <= a.length; i++){
    	ps = ps.delete(w_bd)
    	m[i][0] = ps
    	e[i][0] = i
    }
    ps = start_ps
    for (j = 1; j <= b.length; j++){
    	// console.log(j)
    	ps = ps.insert(w_bd)
    	m[0][j] = ps
    	e[0][j] = j
    }
    for(var i = 1; i <= a.length; i++){
	    for(var j = 1; j <= b.length; j++){
	    	m[i][j] = null
	    	e[i][j] = null
		}
	}
    // console.log(m)
    // console.log(e)

    for(var i = 1; i <= a.length; i++){
	    for(var j = 1; j <= b.length; j++){

	    	// let matches = {}
	    	
		    if(m[i-1][j-1]){
		    	let ps = m[i-1][j-1]
		    	let n_l = ps.s_prefix_graphemes.length
	    		let n_p = ps.w_prefix_phonemes.length
		    	// console.log("TRUE!!!", n_l==i, n_p==j)
		    	let match_set = ps.all_next_matching_ps()
		    	// console.log(match_set)
		    	for (let match of match_set){
		    		// console.log(match)
		    		let d_l = match.s_prefix_graphemes.length-n_l
		    		let d_p = match.w_prefix_phonemes.length-n_p
		    		// console.log("MATCH", match, i+d_l-1,j+d_p-1, "l:", match.lev_dist)
		    		if(!m[i+d_l-1][j+d_p-1] ||
		    		   match.lev_dist < m[i+d_l-1][j+d_p-1].lev_dist){
		    		   	// console.log("WAS Inserted")
		    			m[i+d_l-1][j+d_p-1] = match	
		    		}
		    		
		    	}
		   	}	
		    
		    // matches = [...(m[i][j] || []),...matches]
			if(m[i][j] != null){
				// console.log("!",i,j)
				// m[i][j] = matches; // match
				e[i][j] = e[i-1][j-1]; // match
			}else{
				let subst = e[i-1][j-1]
				let inser = e[i][j-1]
				let delet = e[i-1][j]
				let min = Math.min(subst, inser, delet)
				// console.log(i,j)
				// console.log(min, subst, inser, delet)
				// console.log(w_ps)
				if(subst == min){ //substitute
					ps = m[i-1][j-1].substitute(w_bd)
					e[i][j] = subst + .99// - .5 && isVowel(w_ps.s_prefix_graphemes[j] || "")||0
				}else if(inser == min){ //insert
					ps = m[i][j-1].insert(w_bd)
					e[i][j] = inser + 1 //- .5 && isVowel(w_ps.s_prefix_graphemes[j] || "")||0
				}else{ //delete
					ps = m[i-1][j].delete()
					e[i][j] = delet + 1
				}
				if(!m[i][j] || m[i][j].lev_dist > min + 1){
					m[i][j] = ps
				}
		    }
		    if(m[i][j].length > 1){console.log("HI", i, j, m[i][j])}
		}
	}

	ps = m[a.length][b.length]
	// console.log(ps.w_prefix_phonemes.join(",") + "  /  " + ps.s_prefix_phonemes.join(","))
	// console.log(word, spelling)
	
	let bd = ps.extract_break_down(true)

	console.log("BREAK DOWN:", spelling, bd)
	
	let corr_seq = []
	while(ps.parent != null){
		if(ps.correction != null){
			corr_seq.splice(0,0,ps.correction)
		}
		// console.log(ps)
		ps = ps.parent
	}



	// console.log(corr_seq.map((x)=>x.toString()))
	if(prioritize_consonants){
		corr_seq = favor_consonant_corrections(corr_seq)
	}
	// console.log(corr_seq.map((x)=>x.toString()))
	if(corr_seq.length == 0){corr_seq = [new SpellingCorrection(m[a.length][b.length][0],-1)]}
	console.log("END", corr_seq)
	if(return_break_down){
		return [corr_seq, bd]//m[a.length][b.length][0].extract_break_down(true)]
	}else{
		return corr_seq
	}
	
	
}

function best_partial_spelling(word, spelling, allowSkipInteriorVowels=false){
	if(Array.isArray(word)){word = word.join("")}
	if(Array.isArray(spelling)){spelling = spelling.join("")}
	let phonetic_spelling = word_phonemes[word.toUpperCase()]
	let ps = new PartialSpelling(spelling.toUpperCase(),phonetic_spelling,[],[])
	// console.log(String(ps))
	let leafs = ps.resolve(allowSkipInteriorVowels)
	leafs = leafs.sort((a,b) => b.s_prefix_graphemes.length - a.s_prefix_graphemes.length)

	// console.log(spelling, String(leafs[0]))

	return leafs[0]
	// console.log(spelling, "LEAFS",leafs)
	
}

export function best_break_down(word, spelling){
	if(Array.isArray(word)){word = word.join("")}
	if(Array.isArray(spelling)){spelling = spelling.join("")}
	let phonetic_spelling = word_phonemes[word.toUpperCase()]
	let ps = new PartialSpelling(spelling.toUpperCase(),phonetic_spelling,[],[])
	return ps.find_best_break_down()
}

//remaining_letters -> s_postfix_letters
//remaining_phonemes -> w_postfix_phonemes
//committed_letters -> s_prefix_graphemes
//committed_phones -> w_prefix_phonemes
//present_phones -> s_prefix_phonemes

//A structure that keeps track of all intermediate phonetic interpretations of
// an input spelling. 
class PartialSpelling {
	constructor(s_postfix_letters,
				w_postfix_phonemes,
				s_prefix_graphemes=[],
				w_prefix_phonemes=[],
				lev_dist=0,
				s_prefix_phonemes=[]){
		this.s_postfix_letters = [...s_postfix_letters];
		this.w_postfix_phonemes = w_postfix_phonemes.map((x) => x.replace(/[0-9]/g, ''));
		this.s_prefix_graphemes = [...s_prefix_graphemes]
		this.w_prefix_phonemes = [...w_prefix_phonemes];
		this.s_prefix_phonemes = [...s_prefix_phonemes]
		this.lev_dist = lev_dist
		this.parent = null
		this.correction = null
		this.chain_count = 0
		// console.log("IBI", this.s_prefix_graphemes.slice(-1)[0], this.s_prefix_phonemes.slice(-1)[0])
		this.is_biphoneme_intermediate = (
			(this.s_prefix_graphemes.slice(-1)[0] == "X") &&
			(this.s_prefix_phonemes.slice(-1)[0] != "S")
		)
		// this.true_s_postfix_letters =[...true_s_postfix_letters]
		// console.log("INSTANCE:");
		// console.log(this.s_postfix_letters);
		// console.log(this.w_postfix_phonemes);
		// console.log(this.s_prefix_graphemes);
		// console.log(this.w_prefix_phonemes);
	}

	substitute(w_bd){

		let nxt_g;
		if(this.is_biphoneme_intermediate){
			nxt_g = null
		}else if(this.w_prefix_phonemes.length < w_bd.length){
			nxt_g = w_bd[this.w_prefix_phonemes.length][1].join("") || ""	
			// nxt_g = w_bd[this.chain_count][1].join("") || ""	
		}else{
			nxt_g = default_letters[this.w_postfix_phonemes[0]].toUpperCase()
		}
		// let nxt_g = w_bd[this.w_prefix_phonemes.length][1].join("") || ""
		// console.log("nxt_g", w_bd[this.w_prefix_phonemes.length][1])
		let rl = [...this.s_postfix_letters]; rl.splice(0,1)
		let rp = [...this.w_postfix_phonemes]; rp.splice(0,1)
		let cl = [...this.s_prefix_graphemes, nxt_g]
		let cp = [...this.w_prefix_phonemes,this.w_postfix_phonemes[0]]
		let spp = [...this.s_prefix_phonemes, default_phonemes[this.s_postfix_letters[0]]]
		let ps = new PartialSpelling(rl,rp, cl, cp, this.lev_dist+1, spp)
		
		
		// ps.s_prefix_phonemes = 
		// console.log("SUB",ps.s_prefix_phonemes)
		ps.parent = this
		if(!this.is_biphoneme_intermediate){
			ps.correction = new SpellingCorrection(this, cl.length-1, nxt_g, isVowel(nxt_g),1) 
			ps.correction.insert_phoneme = this.w_postfix_phonemes[0]
			ps.chain_count = this.chain_count+1
		}

		if(nxt_g == "X"){
			ps.is_biphoneme_intermediate = true
			ps.correction.insert_phoneme = ['K','S']
		}

		if(nxt_g == "QU"){
			ps.is_biphoneme_intermediate = true
			ps.correction.insert_phoneme = ['K','W']
		}
		

		return ps
		

	}

	insert(w_bd,w_error=1){
		// console.log("THIS", this)
		let nxt_g;
		if(this.is_biphoneme_intermediate){
			nxt_g = null
		}else if(this.w_prefix_phonemes.length < w_bd.length){
			nxt_g = w_bd[this.w_prefix_phonemes.length][1].join("") || ""	
			// nxt_g = w_bd[this.chain_count][1].join("") || ""	
		}else{
			nxt_g = default_letters[this.w_postfix_phonemes[0]].toUpperCase()
		}
		
		// console.log("nxt_g",nxt_g)
		let rl = [...this.s_postfix_letters]//; rl.splice(0,1)
		let rp = [...this.w_postfix_phonemes]; rp.splice(0,1);
		let cl = nxt_g ? [...this.s_prefix_graphemes, nxt_g] : this.s_prefix_graphemes
		let cp = [...this.w_prefix_phonemes, this.w_postfix_phonemes[0]]
		// console.log(this.s_prefix_phonemes)
		let spp = [...this.s_prefix_phonemes]
		let ps = new PartialSpelling(rl, rp, cl, cp, this.lev_dist+w_error, spp)
		
		
		
		ps.parent = this
		if(!this.is_biphoneme_intermediate){
			ps.correction = new SpellingCorrection(this, cl.length-1,nxt_g, isVowel(nxt_g)) 
			ps.correction.insert_phoneme = this.w_postfix_phonemes[0]
			ps.chain_count = this.chain_count+1
		}

		if(nxt_g == "X"){
			ps.is_biphoneme_intermediate = true
			ps.correction.insert_phoneme = ['K','S']
		}

		if(nxt_g == "QU"){
			ps.is_biphoneme_intermediate = true
			ps.correction.insert_phoneme = ['K','W']
		}
		// console.log("IIIII:",ps.correction.insert_phoneme)
		// console.log(ps.correction.toString())
		return ps
		
	}

	delete(){
		let rl = [...this.s_postfix_letters]; rl.splice(0,1)
		let rp = [...this.w_postfix_phonemes]
		let cl = [...this.s_prefix_graphemes]
		let cp = [...this.w_prefix_phonemes]
		let spp = [...this.s_prefix_phonemes, default_phonemes[this.s_postfix_letters[0]]]
		let ps = new PartialSpelling(rl, rp, cl, cp,this.lev_dist+1, spp)
		ps.chain_count = this.chain_count+1
		// console.log(this.s_prefix_graphemes,this.s_postfix_letters)
		ps.parent = this
		ps.correction = new SpellingCorrection(this, this.s_prefix_graphemes.length,'', isVowel(this.s_postfix_letters[0]||""),1) 
		return ps
	}

	next_ps(n_l, n_p){
		let cl = [...this.s_prefix_graphemes]
		cl.push(...this.s_postfix_letters.slice(0,n_l))
		let cp = [...this.w_prefix_phonemes]
		cp.push(...this.w_postfix_phonemes.slice(0,n_p))

		let spp = [...this.s_prefix_phonemes,this.w_postfix_phonemes[0]]
		let ps = new PartialSpelling(
			this.s_postfix_letters.slice(n_l,n_l+this.s_postfix_letters.length),
			this.w_postfix_phonemes.slice(n_p,n_p+this.w_postfix_phonemes.length),
			cl, cp, this.lev_dist, spp
		)
		ps.chain_count = this.chain_count+1
		ps.parent = this
		// console.log(ps)
		return ps
	}

	all_next_matching_ps (){
		//If there are no more letters to remove then return itself
		if(this.w_postfix_phonemes.length == 0 || this.s_postfix_letters.length == 0){
			return []
		}
		let out = []
		// let did_recurse = false

		//If vowel skipping is enabled then recurse into a branch where
		// //  we skip the vowel if the next phoneme is a vowel
		let next_phoneme1 = this.w_postfix_phonemes[0]
		// if(!isFirst && allowSkipInteriorVowels &&
		//    this.w_postfix_phonemes.length != 1 &&
		//    phoneme_types[next_phoneme1]==='vowel'){
		//  	out.push(this.next_ps(0, 1, out, allowSkipInteriorVowels))
		//  	did_recurse = true
		// }

		//Generate Phoneme/Biphoneme objects for the next target phoneme
		let b_p1 = new Biphoneme(next_phoneme1,"")
		let next_phoneme2 = null;
		let b_p2 = null;
		if(this.w_postfix_phonemes.length > 1){
			next_phoneme2 = this.w_postfix_phonemes[1]
			b_p2 = new Biphoneme(next_phoneme1, next_phoneme2)
			// console.log(b_p2.toString())
		}

		// console.log(b_p1)
		// console.log(b_p2)
		// console.log(grapheme_dict)

		let possible_graphemes1 = grapheme_dict[b_p1]
		let possible_graphemes2 = grapheme_dict[b_p2]
		let rl = this.s_postfix_letters.join("")

		// console.log("<<P", next_phoneme1, ":", possible_graphemes1,possible_graphemes2)
		

		//If the user provided a grapheme (set of letters) that is phonetically
		// correct for the next phoneme then recurse into a brach that commits to that
		if(possible_graphemes1 != null){
			for (let i=0; i < possible_graphemes1.length; i++){
				let g = possible_graphemes1[i];
				// console.log("<<G", g, this.s_postfix_letters.join(""), this.s_postfix_letters.join("").startsWith(g))
				if(rl.startsWith(g)){
					// console.log("g1:",g)
					out.push(this.next_ps(g.length,1))
					// did_recurse = true
				}
			}
			
		}

		//Same thing but for true Biphonemes like X -> K,S
		if(possible_graphemes2 != null){
			for (let i=0; i < possible_graphemes2.length; i++){
				let g = possible_graphemes2[i];
				// console.log(rl, g)
				if(rl.startsWith(g)){
					// console.log("g2:",g)
					out.push(this.next_ps(g.length,2))
					// did_recurse = true
				}
			}
		}
		// console.log(out)

		// //If there isn't a better resolution of the parial spelling
		// //  then just return what we have so far
		// if(out.length > 0){
		// 	return [this]
		// }
		// if(out.length > 0){
		// 	console.log(out)
		// }
		

		return out

	}

	extract_break_down(cull_corrections=false){
		let ps = this
		let out = []
		while(ps.parent != null){
			// if(!ps.is_biphoneme_intermediate){
				let pd, pg
				if(cull_corrections && ps.correction){
					if(ps.correction.remove_length > 0){
						pd = [null]
						if(ps.correction.type == 'replace'){
							pg = Array(ps.correction.remove_length).fill(null)
						}else{
							pg = ps.s_prefix_graphemes.slice(ps.parent.s_prefix_graphemes.length)
						}
					}else if (ps.correction.type == 'insert'){
						ps = ps.parent
						continue
						// pg = [null]
						// pd = [null]
						
					}
				}else{
					pd = ps.w_prefix_phonemes.slice(ps.parent.w_prefix_phonemes.length)
					pg = ps.s_prefix_graphemes.slice(ps.parent.s_prefix_graphemes.length)
				}
				if(pg.length == 0){pg = [null]}
				out.splice(0,0,[pd,pg])
			// }
			
			
			// console.log(ps.s_postfix_letters, pd,pg, (ps.correction && ps.correction.type)||null)
			ps = ps.parent
		}
		// console.log("BD", out)
		return out

	}

	find_best_break_down(){
		let head = [this]
		let keep_going = true
		let finals = []
		while(head.length > 0){
			let new_head = []
			for(let ps of head){
				let matches = ps.all_next_matching_ps()
				// console.log("M",matches)
				for(let match of matches ){
					if(match.w_postfix_phonemes.length > 0){
						new_head.push(match)
					}else{
						finals.push(match)
					}
				}
			}
			head = new_head
		}
		// console.log(finals)
		if(finals.length == 0){
			return null
		}
		let min_post_fix_ind = 0
		let min_postfix = 1000
		let ps;
		for (let i=0; i<finals.length;i++){
			ps = finals[i]
			if(ps.s_postfix_letters.length < min_postfix){
				min_postfix = ps.s_postfix_letters.length
				min_post_fix_ind = i
			}
		}
		ps = finals[min_post_fix_ind]
		return ps.extract_break_down()
		// let matches = .

	}







	toString(){
	 	let out = []
	 	for(let i=0; i < this.w_prefix_phonemes.length; i++){
    		out.push(this.w_prefix_phonemes[i] +":" + this.s_prefix_graphemes[i]);
    	}
    	let rest = "([" + String(this.s_postfix_letters) + "],[" + String(this.w_postfix_phonemes) + "])";
    	return "{" + out.join(",")+ "}" + rest;
	}


	//Helper function used in resolve that makes a new partial spelling and 
	// pushes it to the set of alternative branching phonetic interpretations.
	recurse(n_l, n_p, branches, allowSkipInteriorVowels=false){
		let cl = [...this.s_prefix_graphemes]
		cl.push(...this.s_postfix_letters.slice(0,n_l))
		let cp = [...this.w_prefix_phonemes]
		cp.push(...this.w_postfix_phonemes.slice(0,n_p))

		let ps = new PartialSpelling(
			this.s_postfix_letters.slice(n_l,n_l+this.s_postfix_letters.length),
			this.w_postfix_phonemes.slice(n_p,n_p+this.w_postfix_phonemes.length),
			cl, cp
		)
		if(n_l > 0){
			ps.s_prefix_phonemes = [...this.s_prefix_phonemes,...this.w_postfix_phonemes.slice(0,n_p)]
		}else{
			ps.s_prefix_phonemes = [...this.s_prefix_phonemes]
		}
		
		// console.log("REC", String(ps))

		branches.push(...ps.resolve(allowSkipInteriorVowels,false))
	}


	//Uses dynamic programming to recursively resolve all phonetic 
	//  interpretations of this partial spelling.
	resolve (allowSkipInteriorVowels, isFirst=true){
		//If there are no more letters to remove then return itself
		if(this.w_postfix_phonemes.length == 0 || this.s_postfix_letters.length == 0){
			return [this]
		}
		let out = []
		let did_recurse = false

		//If vowel skipping is enabled then recurse into a branch where
		//  we skip the vowel if the next phoneme is a vowel
		let next_phoneme1 = this.w_postfix_phonemes[0]
		if(!isFirst && allowSkipInteriorVowels &&
		   this.w_postfix_phonemes.length != 1 &&
		   phoneme_types[next_phoneme1]==='vowel'){
		 	this.recurse(0, 1, out, allowSkipInteriorVowels)
		 	did_recurse = true
		}

		//Generate Phoneme/Biphoneme objects for the next target phoneme
		let b_p1 = new Biphoneme(next_phoneme1,"")
		let next_phoneme2 = null;
		let b_p2 = null;
		if(this.w_postfix_phonemes.length > 1){
			next_phoneme2 = this.w_postfix_phonemes[1]
			b_p2 = new Biphoneme(next_phoneme1, next_phoneme2)
		}

		let possible_graphemes1 = grapheme_dict[b_p1]
		let possible_graphemes2 = grapheme_dict[b_p2]

		// console.log("<<P", possible_graphemes1,possible_graphemes2)

		//If the user provided a grapheme (set of letters) that is phonetically
		// correct for the next phoneme then recurse into a brach that commits to that
		if(possible_graphemes1 != null){
			for (let i=0; i < possible_graphemes1.length; i++){
				let g = possible_graphemes1[i];
				// console.log("<<G", g, this.s_postfix_letters.join(""), this.s_postfix_letters.join("").startsWith(g))
				if(this.s_postfix_letters.join("").startsWith(g)){
					this.recurse(g.length,1,out,allowSkipInteriorVowels);
					did_recurse = true
				}
			}
			
		}

		//Same thing but for true Biphonemes like X -> K,S
		if(possible_graphemes2 != null){
			for (let i=0; i < possible_graphemes2.length; i++){
				let g = possible_graphemes2[i];
				if(this.s_postfix_letters.join("").startsWith(g)){
					this.recurse(g.length,2,out,allowSkipInteriorVowels);
					did_recurse = true
				}
			}
		}

		//If there isn't a better resolution of the parial spelling
		//  then just return what we have so far
		if(!did_recurse){
			return [this]
		}


		return out

	}
}

function apply_correction_sequence(corr_seq,spelling){
	// if(!Array.isArray(word)){word = word.split("")}
	if(!Array.isArray(spelling)){spelling = spelling.split("")}
	for(let corr of corr_seq){
		if(corr.insert_index > spelling.length){
			console.log("error")
		}
		if(corr.insert_value){
			spelling.splice(corr.insert_index,corr.remove_length, corr.insert_value)
		}else{
			spelling.splice(corr.insert_index,corr.remove_length)
		}
	}
	return spelling.join('')

}
function check_correction_sequence(word,spelling,favor_consonant){
	let corr_seq = best_correction_sequence(word,spelling,favor_consonant)
	let corrected = apply_correction_sequence(corr_seq,spelling)
	console.log(corr_seq.map(x=>x.toString()))
	console.log(word==corrected,word, spelling, "->",corrected)
}

function check_break_down(word){
	let bd = (new PartialSpelling(word,word_phonemes[word],[],[])).find_best_break_down()
	let s = "{"
	for (let [p,d] of bd){
		s += "[" + p.join(",") + "]" + ":" + d.join("") + ", "
	}
	s += "}"
	console.log("BREAK_DOWN:", s)
}

